.navbar{
    width: 100vw;  /* */
    height: 50px;
    background-color: rgba(3, 3, 0.5);
    position: fixed; /* top과 left가 나옴*/
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    gap: 20px;
    padding: 1rem;
    z-index: 1000;
    font-size: 1.5rem;
}

.navbar a{
    text-decoration: none;
    color: white;
    font-size: 1.5rem;
    font-weight: bold;
    transition: all 0.3s ease;

}

.navbar a:hover{
    color: #ff6347;
}